.projects {
    text-align: center;
    padding: 50px;
  }
  
  .project-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .project {
    margin: 20px;
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 10px;
    max-width: 200px;
  }
  
  .project img {
    width: 100%;
  }
  
  .project h3 {
    font-size: 1.2rem;
  }
  
  .project p {
    margin-top: 10px;
  }
  
  .project a {
    margin: 0 10px;
    text-decoration: none;
    color: #007bff;
  }
  